function getNextNumericId(slides: SlideData[]) {
  const nums = slides
    .map(s => Number.parseInt(s.id, 10))
    .filter(n => Number.isFinite(n))
  const max = nums.length ? Math.max(...nums) : 0
  return String(max + 1)
}

